import Button from '@mui/material/Button';

interface TwoFAButtonProps {
  type: 'continue' | 'getNew';
  disabled?: boolean;
  loading?: boolean;
  onClick: () => void;
}

export default function TwoFAButton({
  type,
  disabled = false,
  loading = false,
  onClick,
}: TwoFAButtonProps) {
  const isGetNew = type === 'getNew';
  const buttonText = isGetNew
    ? 'Get new'
    : loading
      ? 'Verifying...'
      : 'Continue';

  return (
    <Button
      fullWidth
      variant="contained"
      color="primary"
      onClick={onClick}
      disabled={disabled || loading}
      sx={{
        background: disabled ? '#e0e0e0' : '#1976d2',
        color: disabled ? '#999' : '#fff',
        height: '40px',
        textTransform: 'none',
        fontSize: '16px',
        fontWeight: 500,
        '&:hover': {
          background: disabled ? '#e0e0e0' : '#1565c0',
        },
        '&.Mui-disabled': {
          background: '#e0e0e0',
          color: '#999',
        },
      }}
    >
      {buttonText}
    </Button>
  );
}
