import { useState, useRef, useEffect } from 'react';
import { useMutation } from '@tanstack/react-query';
import Typography from '@mui/material/Typography';
import TextField from '@mui/material/TextField';
import Box from '@mui/material/Box';
import { verify2FAMock } from '../lib/apiMocks';
import TwoFAButton from '../shared/ui/TwoFAButton';

interface Props {
  setError: (error: string | null) => void;
}

type TwoFAState = 'initial' | 'expired' | 'invalid' | 'filled';

export default function TwoFAForm({ setError }: Props) {
  const [code, setCode] = useState(['', '', '', '', '', '']);
  const [state, setState] = useState<TwoFAState>('initial');
  const inputs = useRef<HTMLInputElement[]>([]);

  const mutation = useMutation({
    mutationFn: verify2FAMock,
    onSuccess: () => {
      alert('2FA verified!');
    },
    onError: (error: any) => {
      if (error.message.includes('expired')) {
        setState('expired');
      } else {
        setState('invalid');
      }
      setError(error.message);
    },
  });

  const handleChange = (index: number, value: string) => {
    if (value.length > 1 || isNaN(Number(value))) return;
    const newCode = [...code];
    newCode[index] = value;
    setCode(newCode);

    // Сбрасываем состояние ошибки при вводе
    if (state === 'invalid' || state === 'expired') {
      setState('initial');
    }

    // Проверяем заполненность всех полей
    const isCodeFilled = newCode.every((digit) => digit !== '');
    if (isCodeFilled) {
      setState('filled');
    } else {
      setState('initial');
    }

    if (value && index < 5) {
      inputs.current[index + 1]?.focus();
    }
  };

  const handleKeyDown = (index: number, e: React.KeyboardEvent) => {
    if (e.key === 'Backspace' && !code[index] && index > 0) {
      inputs.current[index - 1]?.focus();
    }
  };

  const fullCode = code.join('');

  const handleSubmit = () => {
    mutation.mutate({ code: fullCode });
  };

  const handleGetNew = () => {
    setCode(['', '', '', '', '', '']);
    setState('initial');
    setError(null);
    alert('New code sent!');
    inputs.current[0]?.focus();
  };

  useEffect(() => {
    inputs.current[0]?.focus();
  }, []);

  const getBorderColor = () => {
    if (state === 'invalid' || state === 'expired') return '#ff4d4f';
    return undefined;
  };

  // Определяем, какую кнопку показывать
  const showButton =
    state === 'filled' || state === 'invalid' || state === 'expired';
  const buttonType = state === 'expired' ? 'getNew' : 'continue';
  const isButtonDisabled = state === 'invalid';

  return (
    <>
      <Typography
        variant="h5"
        align="center"
        gutterBottom
        sx={{
          lineHeight: '32px',
          fontSize: '24px',
          fontWeight: 600,
          mb: '8px',
          color: 'rgba(0, 0, 0, 0.88)',
        }}
      >
        Two-Factor Authentication
      </Typography>
      <Typography
        variant="body1"
        align="center"
        sx={{
          fontSize: '16px',
          color: 'rgba(0, 0, 0, 0.88)',
          mb: 3,
          lineHeight: '24px',
        }}
      >
        Enter the 6-digit code from the Google Authenticator app
      </Typography>
      <Box
        sx={{
          display: 'flex',
          justifyContent: 'space-between',
          gap: '8px',
          mb: 1,
        }}
      >
        {code.map((digit, index) => (
          <TextField
            key={index}
            inputRef={(el) => (inputs.current[index] = el)}
            value={digit}
            onChange={(e) => handleChange(index, e.target.value)}
            onKeyDown={(e) => handleKeyDown(index, e)}
            inputProps={{
              maxLength: 1,
              style: { textAlign: 'center', fontSize: '20px', fontWeight: 500 },
            }}
            variant="outlined"
            sx={{
              width: '52.67px',
              '& .MuiOutlinedInput-root': {
                height: '60px',
                '& fieldset': {
                  borderColor: getBorderColor(),
                  borderWidth:
                    state !== 'initial' && state !== 'filled' ? '2px' : '1px',
                },
                '&:hover fieldset': {
                  borderColor:
                    state !== 'initial' && state !== 'filled'
                      ? getBorderColor()
                      : undefined,
                },
                '&.Mui-focused fieldset': {
                  borderColor:
                    state !== 'initial' && state !== 'filled'
                      ? getBorderColor()
                      : undefined,
                },
              },
            }}
          />
        ))}
      </Box>
      {state === 'invalid' && (
        <Typography
          sx={{
            color: '#ff4d4f',
            fontSize: '16px',
            mb: 2,
          }}
        >
          Invalid code
        </Typography>
      )}
      {showButton && (
        <Box sx={{ mt: 3 }}>
          <TwoFAButton
            type={buttonType}
            disabled={isButtonDisabled}
            loading={mutation.isPending}
            onClick={buttonType === 'getNew' ? handleGetNew : handleSubmit}
          />
        </Box>
      )}
    </>
  );
}

